import { By } from 'selenium-webdriver';
import { DriverLike, register } from '@oracle/oraclejet-webdriver';
import { SelectMultipleWebElement } from './SelectMultipleWebElement';
export { SelectMultipleWebElement };

/**
 * Retrieve an instance of [SelectMultipleWebElement](../classes/SelectMultipleWebElement.html).
 * @example
 * ```javascript
 * import { findSelectMultiple } from '@oracle/oraclejet-core-pack/webdriver';
 * const el = await findSelectMultiple(driver, By.id('my-oj-c-select-multiple'));
 * ```
 * @param driver A WebDriver/WebElement instance from where the element will be
 * searched. If WebDriver is passed, the element will be searched globally in the
 * document. If WebElement is passed, the search will be relative to this element.
 * @param by The locator with which to find the element
 */
export async function findSelectMultiple(
  driver: DriverLike,
  by: By
): Promise<SelectMultipleWebElement> {
  const webEl = await driver.findElement(by);
  // Check that the element is of type SelectMultipleWebElement
  if (!(webEl instanceof SelectMultipleWebElement)) {
    const tagName = await webEl.getTagName();
    let supplemental = '';
    if (webEl.constructor.name === 'SelectMultipleWebElement') {
      supplemental = 'You likely have multiple installations of this package.';
    }
    throw Error(
      `findSelectMultiple(${by}) created ${webEl.constructor.name} for <${tagName}>, but expected ${SelectMultipleWebElement.name}. ${supplemental}`
    );
  }
  return webEl;
}

register('oj-c-select-multiple', SelectMultipleWebElement);
